import React, { useState, useCallback, useEffect } from "react";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";
import markerImg from "./m.png";
import styles from "./mapStyling.json";
import countries from "Library/countries.json";
import { _ } from "core-js";

const defsize = 25;

const MapContainer = props => {
	const { context, google } = props;
	const {
		store: { geo = false, markers = [], index },
		setStore
	} = context || {};

	const [cpos, _cpos] = useState(false);

	const [latlng, _latlng] = useState(["49.037868", "31.904297"]);

	const [zoom, _zoom] = useState(9);

	const [state, _state] = useState({
		activeMarker: null
	});

	console.log("GEO", props, google, geo, countries, cpos);

	useEffect(() => {
		if (geo && geo.countryCode) {
			const gpos = countries.filter(
				c => c.country_code === geo.countryCode
			);

			if (gpos.length) {
            _cpos(gpos[0]);
            _latlng([gpos[0].latlng[0], gpos[0].latlng[1]]);
				_zoom(7);
			}
		}
	}, [geo]);

	const markerSets = {
		icon: {
			url: markerImg,
			scaledSize: new google.maps.Size(defsize, defsize)
		}
	};

	const onMarkerClick = (pp, activeMarker, index) => {
		console.log("mk", pp, activeMarker, index);
		_latlng([pp.position.lat, pp.position.lng]);
		_zoom(12);
		setStore({ index: index });

		// activeMarker.animation = pp.google.maps.Animation.BOUNCE;
		// activeMarker.animating = true;
		// console.log("pp, activeMarker,", pp, activeMarker);

		_state({
			activeMarker
		});
	};

	const onMapClicked = props => {
		_state(e => ({
			activeMarker: null
		}));
	};

	const displayMarkers = useCallback(() => {
		return markers.map((m, mx) => (
			<Marker
				labelClass="labelc"
				onClick={(pp, mr) => onMarkerClick(pp, mr, mx)}
				key={mx}
				animation={google.maps.Animation.DROP}
				{...markerSets}
				{...m}
			/>
		));
	}, [markers.length >= 0]);

	return (
		<Map
			{...styles}
			google={google}
			zoom={zoom}
			center={{ lat: latlng[0], lng: latlng[1] }}
			onClick={onMapClicked}>
			{markers.length && displayMarkers()}
		</Map>
	);
};

export default GoogleApiWrapper({
	apiKey: "AIzaSyA4zKpi3hRchkPewoFrU0h_V4a2ykGrohk"
})(MapContainer);
